import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {Options} from 'fullcalendar';
import {CalendrierService} from './calendrier.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {BrouillardsEditComponent} from '../brouillards-edit/brouillards-edit.component';
import {FetchEntityDialogComponent} from '../_shared/fetch-entity-dialog/fetch-entity-dialog.component';
import {DialogData} from '../../../material/popups-and-modals/dialog/dialog.component';
import {Router} from '@angular/router';
import {ModalDismissReasons, NgbCalendar, NgbDateStruct, NgbModal} from '@ng-bootstrap/ng-bootstrap';
const equals = (one: NgbDateStruct, two: NgbDateStruct) =>
	one && two && two.year === one.year && two.month === one.month && two.day === one.day;

const before = (one: NgbDateStruct, two: NgbDateStruct) =>
	!one || !two ? false : one.year === two.year ? one.month === two.month ? one.day === two.day
		? false : one.day < two.day : one.month < two.month : one.year < two.year;

const after = (one: NgbDateStruct, two: NgbDateStruct) =>
	!one || !two ? false : one.year === two.year ? one.month === two.month ? one.day === two.day
		? false : one.day > two.day : one.month > two.month : one.year > two.year;

@Component({
  selector: 'm-calendrier',
  templateUrl: './calendrier.component.html',
  styleUrls: ['./calendrier.component.scss'],
	styles: [`
    .custom-day {
      text-align: center;
      padding: 0.185rem 0.25rem;
      display: inline-block;
      height: 2rem;
      width: 2rem;
	}
    .custom-day.focused {
      background-color: #e6e6e6;
    }
    .custom-day.range, .custom-day:hover {
      background-color: rgb(2, 117, 216);
      color: white;
    }
    .custom-day.faded {
      background-color: rgba(2, 117, 216, 0.5);
    }
 `]
})
export class CalendrierComponent implements OnInit {
	calendarOptions: Options;
	displayEvent: any;
	hoveredDate: NgbDateStruct;
	fromDate: NgbDateStruct;
	toDate: NgbDateStruct;
	closeResult: string;
	title: string;
	start: Date;
	end: Date;
	fullName: string;
	email: string;
	@ViewChild(CalendrierComponent) ucCalendar: CalendrierComponent;
	constructor( calendar: NgbCalendar, private modalService: NgbModal , protected eventService: CalendrierService , public dialog: MatDialog, private router: Router) {
		this.fromDate = calendar.getToday();
		this.toDate = calendar.getNext(calendar.getToday(), 'd', 10);
	}

	ngOnInit() {
		this.eventService.getEvents().subscribe(data => {
			this.calendarOptions = {
				editable: true,
				eventLimit: false,
				navLinks: true,
				locale: 'fr',
				header: {
					left: 'prev,next today',
					center: 'title',
					right: 'month,agendaWeek,agendaDay,listMonth'
				},
				events: data
			};
		});
	}
	clickButton(model: any) {
		this.displayEvent = model;
	}
	eventClick(model: any , conteneur?: any) {
		this.open(conteneur);
		model = {
			event: {
				id: model.event.id,
				start: model.event.start,
				end: model.event.end,
				title: model.event.title,
				allDay: model.event.allDay,
				fullName: model.event.fullName,
				email: model.event.email
				// other params
			},
			duration: {}
		}
		this.title =  model.event.title;
		this.start = model.event.start;
		this.end = model.event.end;
		this.fullName = model.event.fullName;
		this.email = model.event.email;
			this.displayEvent = model;

	}
	updateEvent(model: any) {

		model = {
			event: {
				id: model.event.id,
				start: model.event.start,
				end: model.event.end,
				title: model.event.title
				// other params
			},
			duration: {
				_data: model.duration._data
			}
		}
		this.displayEvent = model;

	}
	open(content) {
		this.modalService.open(content).result.then((result) => {
		}, (reason) => {
			this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
		});
	}

	private getDismissReason(reason: any): string {
		if (reason === ModalDismissReasons.ESC) {
			return 'by pressing ESC';
		} else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
			return 'by clicking on a backdrop';
		} else {
			return  `with: ${reason}`;
		}
	}

	onDateChange(date: NgbDateStruct) {
		if (!this.fromDate && !this.toDate) {
			this.fromDate = date;
		} else if (this.fromDate && !this.toDate && after(date, this.fromDate)) {
			this.toDate = date;
		} else {
			this.toDate = null;
			this.fromDate = date;
		}
	}

	isHovered = date => this.fromDate && !this.toDate && this.hoveredDate && after(date, this.fromDate) && before(date, this.hoveredDate);
	isInside = date => after(date, this.fromDate) && before(date, this.toDate);
	isFrom = date => equals(date, this.fromDate);
	isTo = date => equals(date, this.toDate);


}
