import {Component, Inject, OnInit} from '@angular/core';
import {CustomerEditDialogComponent} from '../customers/customer-edit/customer-edit.dialog.component';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {SpecificationModel} from '../_core/models/specification.model';
import {ActivatedRoute, Router} from '@angular/router';
import {CustomersService, ProductRemarksService, ProductSpecificationsService, ProductsService} from '../_core/services';
import {LayoutUtilsService, MessageType} from '../_core/utils/layout-utils.service';
import {BehaviorSubject, forkJoin, Observable} from 'rxjs';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ListStateModel} from '../_core/utils/list-state.model';
import {map, startWith} from 'rxjs/operators';
import {TypesUtilsService} from '../_core/utils/types-utils.service';
import {SpecificationsService} from '../_core/services/specification.service';
import {SubheaderService} from '../../../../../../core/services/layout/subheader.service';
import {ProductModel} from '../_core/models/product.model';
import {CustomerModel} from '../_core/models/customer.model';
import {FileSystemDirectoryEntry, FileSystemFileEntry, UploadEvent, UploadFile} from 'ngx-file-drop';
@Component({
  selector: 'm-brouillards-edit',
  templateUrl: './brouillards-edit.component.html',
  styleUrls: ['./brouillards-edit.component.scss']
})
export class BrouillardsEditComponent implements OnInit {
	product: ProductModel;
	loadingAfterSubmit: boolean;
	public files: UploadFile[] = [];
	test = false;
	tests = true;
	oldProduct: ProductModel;
	viewLoading: boolean = false;
	selectedTab: number = 0;
	loadingSubject = new BehaviorSubject<boolean>(false);
	loading$ = this.loadingSubject.asObservable();
	productForm: FormGroup;
	hasFormErrors: boolean = false;
	remarksListState: ListStateModel;
	specsListState: ListStateModel;
	availableYears: number[] = [];


	constructor(public dialogRef: MatDialogRef<CustomerEditDialogComponent>,
				@Inject(MAT_DIALOG_DATA) public data: any,
				private activatedRoute: ActivatedRoute,
				private router: Router,
				private productsService: ProductsService,
				private remarksService: ProductRemarksService,
				private specificationsService: SpecificationsService,
				private productSpecificationsService: ProductSpecificationsService,
				private typesUtilsService: TypesUtilsService,
				private productFB: FormBuilder,
				public dialog: MatDialog,
				private subheaderService: SubheaderService,
				private layoutUtilsService: LayoutUtilsService) { }

	ngOnInit() {
		this.product = this.data.product;
		this.createForm();

		/* Server loading imitation. Remove this on real code */
		this.viewLoading = true;
		setTimeout(() => {
			this.viewLoading = false;
		}, 1000);
	}

	initProduct() {
		this.createForm();
		this.loadLists();
		this.loadingSubject.next(false);
		if (!this.product.id) {
			this.subheaderService.setBreadcrumbs([
				{ title: 'Brouillard', page: '/brouillard' },
			]);
			return;
		}
		this.subheaderService.setTitle('Éntre un bon');
		this.subheaderService.setBreadcrumbs([
			{ title: 'eCommerce', page: '/brouillard' },
			{ title: 'Products',  page: '/brouillard/products' },
			{ title: 'Edit product', page: '/brouillard/products/edit', queryParams: { id: this.product.id } }
		]);
	}

	createForm() {
		this.product.date = this.typesUtilsService.getDateFromString(this.product.dateOfBbirth);
		this.productForm = this.productFB.group({
			depense: [this.product.depense, [Validators.required, Validators.pattern(/^-?(0|[1-9]\d*)?$/)]],
			recette: [this.product.recette, [Validators.required, Validators.pattern(/^-?(0|[1-9]\d*)?$/)]],
			description: [this.product.description , Validators.required],
			date: [this.product.date , Validators.required],
			solde: [this.product.solde, [Validators.required, Validators.pattern(/^-?(0|[1-9]\d*)?$/)]],
			condition: [this.product.condition.toString(), [Validators.required, Validators.min(0), Validators.max(1)]],
			status: [this.product.status.toString(), [Validators.required, Validators.min(-1), Validators.max(1)]]
		});

	}

	loadLists() {
		this.remarksListState = new ListStateModel(this.product.id);
		this.specsListState = new ListStateModel(this.product.id);
	}
	goBack(id = 0) {
		let _backUrl = 'brouillard/products';
		if (id > 0) {
			_backUrl += '?id=' + id;
		}
		this.router.navigateByUrl(_backUrl);
	}

	refreshProduct(id = 0) {
		const _refreshUrl = 'brouillard/products/edit?id=' + id;
		this.router.navigateByUrl(_refreshUrl);
	}

	reset() {
		this.product = Object.assign({}, this.oldProduct);
		this.createForm();
		this.hasFormErrors = false;
		this.productForm.markAsPristine();
		this.productForm.markAsUntouched();
		this.productForm.updateValueAndValidity();
	}
	onSumbit() {
		this.hasFormErrors = false;
		this.loadingAfterSubmit = false;
		const controls = this.productForm.controls;
		/** check form */
		if (this.productForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedProduct = this.prepareProduct();
		if (editedProduct.id > 0) {
			this.updateCustomer(editedProduct);
		} else {
			this.createCustomer(editedProduct);
		}
	}

	updateCustomer(_product: ProductModel) {
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
		this.productsService.updateProduct(_product).subscribe(res => {
			/* Server loading imitation. Remove this on real code */
			this.viewLoading = false;
			this.viewLoading = false;
			this.dialogRef.close({
				_product,
				isEdit: true
			});
		});
	}

	createCustomer(_customer: ProductModel) {
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
		this.productsService.createProduct(_customer).subscribe(res => {
			this.viewLoading = false;
			this.dialogRef.close({
				_customer,
				isEdit: false
			});
		});
	}

	onAlertClose($event) {
		this.hasFormErrors = false;
	}
	prepareProduct(): ProductModel {
		const controls = this.productForm.controls;
		const _product = new ProductModel();
		_product.id = this.product.id;
		_product.date = controls['date'].value;
		_product.description = controls['description'].value;
		_product.solde = +controls['solde'].value;
		_product.recette = +controls['recette'].value;
		_product.depense = +controls['depense'].value;
		_product.condition = +controls['condition'].value;
		_product.status = +controls['status'].value;
		return _product;
	}
	/** UI */
	getTitle(): string {
		if (this.product.id > 0) {
			return `Modifier le bon N°: ${this.product.id}`;
		}

		return 'Nouveau Employée';
	}
	isEntre() {
		this.test = true;
		this.tests = false;
	}
	isSortie() {
		this.test = false;
		this.tests = true;
	}

	public dropped(event: UploadEvent) {
		this.files = event.files;
		for (const droppedFile of event.files) {

			// Is it a file?
			if (droppedFile.fileEntry.isFile) {
				const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
				fileEntry.file((file: File) => {

					// Here you can access the real file
					console.log(droppedFile.relativePath, file);

					/**
					 // You could upload it like this:
					 const formData = new FormData()
					 formData.append('logo', file, relativePath)

					 // Headers
					 const headers = new HttpHeaders({
            'security-token': 'mytoken'
          })

					 this.http.post('https://mybackend.com/api/upload/sanitize-and-save-logo', formData, { headers: headers, responseType: 'blob' })
					 .subscribe(data => {
            // Sanitized logo returned from backend
          })
					 **/

				});
			} else {
				// It was a directory (empty directories are added, otherwise only files)
				const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
				console.log(droppedFile.relativePath, fileEntry);
			}
		}
	}

	public fileOver(event) {
		console.log(event);
	}

	public fileLeave(event) {
		console.log(event);
	}
}
