import { BaseModel } from './_base.model';

export class CustomerModel  extends BaseModel {
	id: number;
	prenom: string;
	nom: string;
	email: string;
	userName: string;

	nbJourRestant: number;
	status: number; // 0 = Active | 1 = Suspended | Pending = 2
	dateOfBbirth: string;
	dateRecrut: string;
	dateDeNaissance: Date;
	dateDeRecrutement: Date;
	type: number; // 0 = Business | 1 = Individual

	clear() {
		this.dateDeNaissance = new Date();
		this.prenom = '';
		this.nom = '';
		this.email = '';
		this.userName = '';
		this.nbJourRestant = 0;
		this.type = 1;
		this.status = 0;
		this.dateDeRecrutement = new Date();
	}
}
