import {Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
// Material
import { MatPaginator, MatSort, MatDialog } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
// RXJS
import { debounceTime, distinctUntilChanged, tap } from 'rxjs/operators';
import { fromEvent, merge } from 'rxjs';
// Services
import { ProductsService } from '../../_core/services/index';
import { LayoutUtilsService, MessageType } from '../../_core/utils/layout-utils.service';
import { SubheaderService } from '../../../../../../../core/services/layout/subheader.service';
// Models
import { ProductModel } from '../../_core/models/product.model';
import { ProductsDataSource } from '../../_core/models/data-sources/products.datasource';
import { QueryParamsModel } from '../../_core/models/query-models/query-params.model';
import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {CustomerEditDialogComponent} from '../../customers/customer-edit/customer-edit.dialog.component';
import {ProductEditComponent} from '../product-edit/product-edit.component';
import {CustomerModel} from '../../_core/models/customer.model';
import {TranslateService} from '@ngx-translate/core';
import {BrouillardsEditComponent} from '../../brouillards-edit/brouillards-edit.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

// Table with EDIT item in new page
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/components/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	selector: 'm-products-list',
	templateUrl: './products-list.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProductsListComponent implements OnInit {
	// Table fields
	@ViewChild('TABLE') table: ElementRef;
	product: ProductModel[];
	@Input() nameBrouil: string;
	bon: ProductModel;
	date: Date;
	id: number;
	typeBon: number;
	solde: number;
	depense: number;
	description: string;
	type: number;
	recette: number;
	dataSource: ProductsDataSource;
	displayedColumns = ['select', 'condition' , 'date', 'description', 'recette', 'depense', 'solde',  'status', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	// Filter fields
	@ViewChild('searchInput') searchInput: ElementRef;
	filterStatus: string = '';
	filterCondition: string = '';
	// Selection
	selection = new SelectionModel<ProductModel>(true, []);
	productsResult: ProductModel[] = [];
	constructor(
				private productsService: ProductsService,
				public dialog: MatDialog,
				private modalService: NgbModal,
				public translate: TranslateService,
				private route: ActivatedRoute,
				private subheaderService: SubheaderService,
				private layoutUtilsService: LayoutUtilsService) { }

	/** LOAD DATA */
	ngOnInit() {
		// If the user changes the sort order, reset back to the first page.
		this.sort.sortChange.subscribe(() => (this.paginator.pageIndex = 0));

		/* Data load will be triggered in two cases:
		- when a pagination event occurs => this.paginator.page
		- when a sort event occurs => this.sort.sortChange
		**/
		merge(this.sort.sortChange, this.paginator.page)
			.pipe(
				tap(() => {
					this.loadProductsList();
				})
			)
			.subscribe();

		// Filtration, bind to searchInput
		fromEvent(this.searchInput.nativeElement, 'keyup')
			.pipe(
				debounceTime(150),
				distinctUntilChanged(),
				tap(() => {
					this.paginator.pageIndex = 0;
					this.loadProductsList();
				})
			)
			.subscribe();

		// Set title to page breadCrumbs
		this.subheaderService.setTitle('Brouillard');
		// Init DataSource
		this.dataSource = new ProductsDataSource(this.productsService);
		let queryParams = new QueryParamsModel({});
		// Read from URL itemId, for restore previous state
		this.route.queryParams.subscribe(params => {
			if (params.id) {
				queryParams = this.productsService.lastFilter$.getValue();
				this.restoreState(queryParams, +params.id);
			}
			// First load
			this.dataSource.loadProducts(queryParams);
		});
		this.dataSource.entitySubject.subscribe(res => this.productsResult = res);
	}

	loadProductsList() {
		this.selection.clear();
		const queryParams = new QueryParamsModel(
			this.filterConfiguration(),
			this.sort.direction,
			this.sort.active,
			this.paginator.pageIndex,
			this.paginator.pageSize
		);
		this.dataSource.loadProducts(queryParams);
		this.selection.clear();
	}

	/** FILTRATION */
	filterConfiguration(): any {
		const filter: any = {};
		const searchText: string = this.searchInput.nativeElement.value;

		if (this.filterStatus && this.filterStatus.length > 0) {
			filter.status = +this.filterStatus;
		}

		if (this.filterCondition && this.filterCondition.length > 0) {
			filter.condition = +this.filterCondition;
		}

		filter.recette = searchText;

		filter.depense = searchText;
		filter.solde = searchText;
		filter.description = searchText;
		return filter;
	}

	restoreState(queryParams: QueryParamsModel, id: number) {
		if (id > 0) {
			this.productsService.getProductById(id).subscribe((res: ProductModel) => {
				const message = res._createdDate === res._updatedDate ?
					`New product successfully has been added.` :
					`Product successfully has been saved.`;
				this.layoutUtilsService.showActionNotification(message, res._isNew ? MessageType.Create : MessageType.Update, 10000, true, false);
			});
		}

		if (!queryParams.filter) {
			return;
		}

		if ('condition' in queryParams.filter) {
			this.filterCondition = queryParams.filter.condition.toString();
		}

		if ('status' in queryParams.filter) {
			this.filterStatus = queryParams.filter.status.toString();
		}

		if (queryParams.filter.model) {
			this.searchInput.nativeElement.value = queryParams.filter.model;
		}
	}

	/** ACTIONS */
	/** Delete */
	deleteProduct(_item: ProductModel) {
		const _title: string = 'Suppression du Bon';
		const _description: string = 'Êtes-vous sûr de supprimer définitivement ce bon?';
		const _waitDesciption: string = 'Le bon est en train d\'etre supprimer ...';
		const _deleteMessage = `Le bon a été supprimé.`;

		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.productsService.deleteProduct(_item.id).subscribe(() => {
				this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
				this.loadProductsList();
			});
		});
	}

	deleteProducts() {
		const _title: string = 'Suppression des bons';
		const _description: string = 'Êtes-vous sûr de supprimer définitivement ces bons?';
		const _waitDesciption: string = 'Les bons sont en train d\'etre supprimer ...';
		const _deleteMessage = 'Les bons sélectionnés ont été supprimés';

		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			const idsForDeletion: number[] = [];
			for (let i = 0; i < this.selection.selected.length; i++) {
				idsForDeletion.push(this.selection.selected[i].id);
			}
			this.productsService.deleteProducts(idsForDeletion).subscribe(() => {
				this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
				this.loadProductsList();
				this.selection.clear();
			});
		});
	}

	fetchProducts() {
		// tslint:disable-next-line:prefer-const
		let messages = [];
		this.selection.selected.forEach(elem => {
			messages.push({
				solde: `${elem.solde}`,
				recette: ` ${elem.recette}`,
				depense: `${elem.depense}`,
				date: `${elem.date}`,
				description: `${elem.description}`,
				id: elem.id,
				condition: elem.condition
			});
		});
		this.layoutUtilsService.fetchElements(messages);
	}

	/** Update Product */
	updateStatusForProducts() {
		const _title = 'Statut de mise à jour pour les bons sélectionnés';
		const _updateMessage = 'Le statut a été mis à jour pour les bons sélectionnés';
		const _statuses = [{ value: 0, text: 'En attente' }, { value: 1, text: 'Validé' } , {value: -1 , text: 'Rejeté'}];
		const _messages = [];

		this.selection.selected.forEach(elem => {
			_messages.push({
				text: `${elem.solde} ${elem.description} ${elem.recette} ${elem.depense} ${elem.date} ${elem.condition}`,
				id: elem.id,
				status: elem.status,
				statusTitle: this.getItemStatusString(elem.status),
				statusCssClass: this.getItemCssClassByStatus(elem.status)
			});
		});

		const dialogRef = this.layoutUtilsService.updateStatusForCustomers(_title, _statuses, _messages);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				this.selection.clear();
				return;
			}

			this.productsService.updateStatusForProduct(this.selection.selected, +res).subscribe(() => {
				this.layoutUtilsService.showActionNotification(_updateMessage, MessageType.Update);
				this.loadProductsList();
				this.selection.clear();
			});
		});
	}

	/** SELECTION */
	isAllSelected() {
		const numSelected = this.selection.selected.length;
		const numRows = this.productsResult.length;
		return numSelected === numRows;
	}

	/** Selects all rows if they are not all selected; otherwise clear selection. */
	masterToggle() {
		if (this.isAllSelected()) {
			this.selection.clear();
		} else {
			this.productsResult.forEach(row => this.selection.select(row));
		}
	}

	/* UI */
	getItemStatusString(status: number = 0): string {
		switch (status) {
			case 1:
				return 'Validé';
			case 0:
				return 'En attente';
			case -1:
				return 'Rejeté';
		}
		return '';
	}

	getItemCssClassByStatus(status: number = 0): string {
		switch (status) {
			case 1:
				return 'success';
			case 0:
				return 'metal';
			case -1:
				return 'danger';
		}
		return '';
	}

	getItemConditionString(condition: number = 0): number {
		switch (condition) {
			case 0:
				return 0;
			case 1:
				return 1;
		}
		return;
	}

	getItemCssClassByCondition(condition: number = 0): string {
		switch (condition) {
			case 0:
				return 'primary';
			case 1:
				return 'accent';
		}
		return '';
	}
	addBrouillard() {
		const newProducts = new ProductModel();
		newProducts.clear(); // Set all defaults fields
		this.editProduct(newProducts);
	}

	/** Edit */
	editProduct(product: ProductModel) {
		let saveMessageTranslateParam = 'ECOMMERCE.CUSTOMERS.EDIT.';
		saveMessageTranslateParam += product.id > 0 ? 'UPDATE_MESSAGE' : 'ADD_MESSAGE';
		const _saveMessage = this.translate.instant(saveMessageTranslateParam);
		const _messageType = product.id > 0 ? MessageType.Update : MessageType.Create;
		const dialogRef = this.dialog.open(BrouillardsEditComponent, { data: { product } });
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}
			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType, 10000, true, false);
			this.loadProductsList();
		});
	}
	openSmall(content , id?: number) {
			this.productsService.getProductById(id).subscribe(
				(data: ProductModel) => {
					this.date = data['date'];
					this.id = data['id'];
					this.description = data['description'];
					this.recette = data['recette'];
					this.depense = data['depense'];
					this.solde = data['solde'];
					this.typeBon = data['condition'];


				}
			);
			 this.modalService.open(content, {
					 size: 'sm'
			 });
	 }
}
